import React, { Component } from 'react'
import axios from 'axios'
import { isEqual } from 'lodash'
import BuilderElement from './BuilderElement/BuilderElement'

if (process.env.NODE_ENV === 'development') {
    axios.defaults.baseURL = 'http://localhost:5000'
}

axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.put['Content-Type'] = 'application/json'
axios.defaults.headers.delete['Content-Type'] = 'application/json'
axios.defaults.headers.common.Accept = 'application/json'

class App extends Component {
    state = {
        resourcesObjects: {},
        page: '',
        structure: [],
        pagesStructure: null,
        pluginsStructure: [],
        filesStructure: [],
        baseUrl: '',
    }

    componentDidMount() {
        return axios
            .post(
                'https://api.websiter.dev/api/resources/live',
                JSON.stringify({
                    url: window.location.href,
                })
            )
            .then(response => {
                this.setState({
                    ...response.data,
                    structure:
                        response.data.resourcesObjects[response.data.page]
                            .structure,
                })
            })
            .catch(err => {})
    }
    render() {
        return this.state.structure
            .filter(itemInn =>
                isEqual(itemInn.path, [
                    this.state.structure.filter(
                        item => item.path.length === 0
                    )[0].id,
                    this.state.structure.filter(
                        item => item.path.length === 1
                    )[1].id,
                ])
            )
            .map(itemInn => (
                <BuilderElement
                    key={itemInn.id}
                    pagesStructure={this.state.pagesStructure}
                    structure={this.state.structure}
                    element={itemInn}
                    pluginsStructure={this.state.pluginsStructure}
                    resourcesObjects={this.state.resourcesObjects}
                    resourceDraft={
                        this.state.page
                            ? this.state.resourcesObjects[this.state.page]
                            : {}
                    }
                    currentResource={this.state.page}
                    filesStructure={this.state.filesStructure}
                    baseUrl={this.state.baseUrl}
                />
            ))
    }
}
